<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>在vuepress中，你可以在博客中，使用你本地的图片，或者是其他的静态文件，如字体，css，视频等等，但是必须要将这些静态文件放入到<code>docs/.vuepress/public</code>文件夹中</p>
</div>
<p>加入现在在该<code>public</code>目录下，存在如下文件及文件夹，目前博客启动的链接为<code>http://localhost:8080/</code>(你的不一定跟我一样)</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211208161259246" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211208161259246.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211208161655614" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211208161655614.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>那么这些静态文件，可以通过下面这些方式访问</p>
<ul>
<li><code>public/avatar.jpg</code> http://localhost:8080/avatar.jpg</li>
<li><code>public/bg/j.jpg</code> http://localhost:8080/bg/1.jpg</li>
<li><code>public/bg/animate/2.jpg</code> http://localhost:8080/bg/animate/2.jpg</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>上面的<code>public/avatar.jpg</code>是什么？</p>
<blockquote>
<p>这是<code>public</code>目录下的<code>avatar.jpg</code>文件</p>
</blockquote>
</div>
<div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<p>请注意，<code>public</code>目录是静态文件目录，加入该目录下有<code>avatar.jpg</code>文件，访问的时候，直接<code>/avatar.jpg</code>，不需要像这样<code>/public/avatar.jpg</code>(没有任何用),如果该<code>avatar.jpg</code>文件，在<code>public/avatar/</code>目录下，那么访问路径就是<code>/avatar/avatar.jpg</code></p>
</div>
<h2 id="背景图片使用本地" tabindex="-1"><a class="header-anchor" href="#背景图片使用本地" aria-hidden="true">#</a> 背景图片使用本地</h2>
<p>如果对如何访问本地静态文件，还是不清楚，请自己在<code>docs/.vuepress/public</code>目录下，放几张图片，按照上面的方式自己访问，自己测试一下，容易理解</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211208163212971" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211208163212971.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>因为<code>public/bg/3.jpg</code>文件是在本地，所以我们在配置背景图片的时候，可以直接<code>/bg/3.jpg</code>就可以访问到了，该图片因为可以直接在浏览器中输入<code>http://localhost:8080/bg/3.jpg</code>(端口不一定是8080)进行访问，所以你也可以直接在背景图片那里输入<code>http://localhost:8080/bg/3.jpg</code>，但是不推荐，当你部署之后，此<code>http://localhost:8080/bg/3.jpg</code>并不能打开<code>3.jpg</code>图片，因为<code>localhost</code>只能在本地打开</p>
</div>
